        text-overflow: ellipsis;
      }
      &:nth-child(1) {
        /*width: 40px;*/
        /*max-width: 40px;*/
        width: 35%;
        max-width: 200px;
      }
      &:nth-child(2) {
        width: 15%;
        /*flex: 2;*/
        /*max-width: 150px;*/
      }
      &:nth-child(3) {
        width: 25%;
        max-width: 350px;
        flex: 1;
      }
      &:nth-child(4) {
        /*width: 20%;*/
        /*max-width: 120px;*/
        width: 180px;
        flex: unset;
        p {
          font-size: 12px;

          span {
            color: $color-primary;
            cursor: pointer;

            &:hover {
              text-decoration: underline;
            }
          }
        }
      }
      &:last-child {
        margin-right: 0;
      }
    }
    &:last-child {
      margin-bottom: 0;
    }
  }
  .income-heading-row {
    margin-bottom: 10px;
    div {
      h3 {
        color: #5469d4;
        /*font-weight: 600;*/
      }
    }
  }
  .income-heading-row {
    margin-bottom: 20px;
    div {
      h3 {
        color: #5469d4;
        /*font-weight: 600;*/
      }
    }
  }
  .income-heading {
    .with-icon {
      align-self: center;
      justify-self: center;
      text-align: center;
    }
  }
  .icon-product {
    font-size: 24px;
  }
  .archived {
    display: flex;
    align-items: center;

    input {
      width: 12px;
      height: 12px;
      margin-bottom: 0;
      margin-top: 0;
      border-color: #444;
    }
    a {
      font-size: 10px;
      margin-left: 4px;
      margin-right: 0;
      color: #444;
    }
  }
  .fulfill-order-after {
    display: flex;
    justify-content: center;
    span {
      cursor: pointer;
      &:hover {
        text-decoration: underline;
      }
    }
  }
  .title-box {
    flex: 1;
    width: 100%;

    h3 {
      margin-bottom: 15px;
    }
    &:last-child {
      h3 {
        margin-bottom: 0;
      }
    }
  }
  ._abc {
    align-items: flex-start;
  }
  .responsive-title {
    display: none;
  }
  .responsive-order-box {
    display: none;
  }
  .x-wrap {
    display: none;
  }
  .order-fulfilled-wrap {
    display: flex;
    margin-bottom: 10px;

    &:last-child {
      margin-bottom: 0;
    }
    p {
      color: $color-primary;
      font-size: 22px !important;
      font-weight: 600;
      text-transform: uppercase;
      margin-bottom: 0 !important;
    }
  }
  $order-fulfilled-icon-height: 30px;
  .order-fulfilled {
    width: $order-fulfilled-icon-height;
    height: $order-fulfilled-icon-height;
    margin-right: 8px;
  }
  $download-file-icon-height: 24px;
  .download-file {
    width: $download-file-icon-height;
    height: $download-file-icon-height;
    margin-right: 8px;
  }
  .special-box {    
    padding-left: 40px;
    padding-right: 40px;
    margin-bottom: 12px;
  }
  .special-box-2 {
    background-color: $light-grey;
    padding-top: 8px;
    padding-left: 19px;
    padding-right: 19px;
    padding-bottom: 32px;
  }
  @media (max-width: 680px) {
    .fulfill-order-after {
      /*width: 100%;*/
      justify-self: center;
    }
    .x-wrap {
      display: flex;
      .responsive-order-box {
        display: flex;
        margin-bottom: 20px;

        &:last-child {
          margin-bottom: 0;
        }
      }
    }
    .order-box {
      /*display: none;*/
      flex-direction: column !important;
    }
    .responsive-order-box-last {
      margin-bottom: 0;
    }
    .income-row {
      height: unset;
      flex-direction: column;
      min-width: unset;

      p {
        font-size: 14px !important;
        margin-bottom: 10px;
      }

      .no-attachments {
        font-size: 12px !important;
        font-weight: 400 !important;
        margin-bottom: 0 !important;
      }

      .responsive-order-name {
        letter-spacing: 0.01em !important;
        font-size: 18px !important;
        color: $color-primary;
        font-weight: 600;
        letter-spacing: 0.01em;
        margin-bottom: 10px !important;
      }

      .income-heading {
        width: 100% !important;
        max-width: unset !important;
        margin-right: unset;
      }

      .responsive-title {
        display: flex;
        flex: 1;
      }
    }
    .order-fulfilled-wrap {
      display: flex;
      p {
        color: $color-primary;
        font-size: 15px !important;
        font-weight: 600;
        text-transform: uppercase;
        margin-bottom: 0 !important;
      }
    }
    .income-heading-row {
      display: none;
    }
  }
  .whole-page-box-global-2-bigger {
    padding-bottom: 20px;
    height: unset;
  }
  @media (max-width: 425px) {
    .whole-page-box-global-2-bigger {
      height: unset;
      padding-top: 30px;
      padding-bottom: 30px;
    }
    .title-right {
      text-align: center;
      margin-right: 0;
      margin-top: 8px;
      margin-bottom: 7px;
    }
    .title-box {
      /*justify-content: center;*/
      align-items: center;
      /*flex-direction: column;*/
    }
    .special-box {
      padding-top: 18px;
      padding-bottom: 18px;

      h3 {
        margin-top: 8px;
        margin-bottom: 7px;
      }
    }
  }

  .one-order {
    margin-bottom: 30px;

    p {
      color: $color-background-dark;
    }

    &:last-child {
      margin-bottom: 0;
    }
  }
  .responsive-order-box {
    .income-heading {
      p {
        letter-spacing: 0.02em;
      }
      p:first-child {
        margin-bottom: 4px;
        font-weight: 600;
      }
      p:last-child {
        margin-bottom: 4px;
      }
    }
  }
  .instructions {
    display: flex;
    flex-direction: column;
    align-items: flex-start !important;

    p:first-child {
      /*flex: unset !important;*/
      /*margin-bottom: 4px;*/
      /*font-weight: 600;*/
    }
    p:last-child {
      /*margin-bottom: 4px;*/
      /*font-weight: 600;*/
      /*margin-bottom: 8px !important;*/
    }
  }
  .instructions2 {
    flex-direction: row;
    align-items: center !important;
    /*justify-content: center;*/
    margin-bottom: 16px;

    p {
      flex: 1 !important;
    }

    a {
      font-size: 14px !important;
      font-weight: 600;
      color: $color-primary;
      margin-bottom: 0 !important;
    }
  }
  .fu {
    flex: unset !important;
    margin-right: 6px;
  }

  .end {
    font-size: 14px;
    margin-top: 25px;
    /*margin-bottom: 10px;*/
    p {
      text-align: center;
    }
  }
  .end-not-empty {
    margin-top: 20px;
  }
  .archived {
    /*margin-top: 1px;*/
  }

  .anti-hover {
    text-decoration: none !important;

    & .mini-button {
      text-decoration: none !important;
    }
  }

  .sort-by {
    font-size: 12px;
    span {
      text-decoration: underline;
      color: $color-primary; 
    }
  }

  .sort-select {
    position: absolute;
    right: 0;
    background-color: #fff;
    padding: 25px 50px;
    border-radius: 8px;
    border: 1px solid #eaeaea;

    p {
      cursor: pointer;
      margin-bottom: 12px;
      font-size: 14px;
      font-weight: 500;
      color: $color-primary;
      text-align: center;

      &:last-child {
        margin-bottom: 0;
      }
    }
  }

  .aifsi {
    align-items: flex-start !important;
  }

  .product-detail-tabs {
    &-tab {
      padding: 20px;
      border-top-left-radius: 15px;
      border-top-right-radius: 15px;

      /*background-color: #fdfdfd;*/
      a {
        color: $color-background-dark;
        font-weight: 700;
        /*font-weight: 16px;*/
        text-transform: lowercase;
        letter-spacing: 0.02em;
        font-weight: 400;
      }
      &-active {
        background-color: $color-white;
        -webkit-box-shadow: 0 2px 3px 1px rgba(8, 8, 8, .07);
        box-shadow: 0 2px 3px 1px rgba(8, 8, 8, .07);
        a {
          }
      }
    }
  }

  .product-detail-box {
    background-color: $color-white;
    padding: 20px;
    letter-spacing: 0.5px;
    border-bottom-left-radius: 15px;
    border-bottom-right-radius: 15px;
    -webkit-box-shadow: 0 2px 3px 1px rgba(8, 8, 8, .07);
    box-shadow: 0 2px 3px 1px rgba(8, 8, 8, .07);
  }

  .fulfillment-item-each {
    & > div {
      width: 150px;
      /*min-width: 100px;*/
    }
  }
  .end {
    p {
      color: $color-background-dark;
    }
  }
  .existing-product-image {
    width: 25px;
    object-fit: contain;

    &-wrapper {
      height: 35px;
      width: 35px;
      min-height: 35px;
      min-width: 35px;
      max-height: 35px;
      max-width: 35px;
      display: flex;
      justify-content: center;
      align-items: center;
      border-radius: 10px;
      background-color: #DDD;
      /*border: 1px solid $color-background-dark;*/
      margin-right: 15px;
    }
  }

  @media (max-width: 480px) {
    .product-detail-tabs {
      margin-bottom: 20px;
      flex-direction: column;
      &-tab {
        border-bottom-left-radius: 15px;
        border-bottom-right-radius: 15px;
      }
    }
    .product-detail-box {
        border-top-left-radius: 15px;
        border-top-right-radius: 15px;
    }
  }

  .button-smaller {
    max-width: 100px;
  }

</style>



