<template>
  <div class="w100p df fdc aic admin-wrapper">

    <div class="w100p df fdc admin-section">
      <h3>Sales</h3>
      <div class="w100p df fdc all-sales">
        <div class="one-user" v-for="(user, u) in sales">
          <p class="text-dark">{{u}}</p>
          <div class="one-sale" v-for="(sale, s) in user">
            {{sale.fulfillmentDetails.email}}
          </div>
        </div>
      </div>
    </div>

    <div class="w100p df fdc admin-section">
      <h3>Collected Emails</h3>
      <div class="w100p df fdc all-sales">
        <div class="one-user" v-for="(user, u) in emails">
          <p class="text-dark">{{u}}</p>
          <div class="one-sale" v-for="(sale, s) in user">
            {{sale.email}}
          </div>
        </div>
      </div>
    </div>

    <div class="w100p df fdc admin-section">
      <h3>Stores</h3>
      <div class="w100p df fdc all-sales">
        <div class="one-user" v-for="(user, u) in stores">
          <p class="text-dark">{{u}}</p>
          <div class="one-sale" v-for="(item, i) in user.items">
            {{item.title}}
          </div>
        </div>
      </div>
    </div>

    <div class="w100p df fdc admin-section">
      <h3>Products</h3>
      <div class="w100p df fdc all-sales">
        <div class="one-user w100p" v-for="(user, u) in products">
          <p class="text-dark w100p">{{usernamesById[u] || '-'}}</p>
          <div class="one-sale df jcsb aic w100p" v-for="(product, p) in user" v-if="product.created > 1696097114947">
            <!-- <img class="product-image" :src="product.productImageUrl || ''"> -->
            <p>{{product.name}}</p>
            <div class="f1"></div>
            <p class="ml20">{{date(product.created)}}</p>
            <h4 class="go-to-button" @click="goTo('/' + usernamesById[u] + '/' + product.uniqueProductId)">See Product</h4>
          </div>
        </div>
      </div>
    </div>

  </div>
</template>

<script>

import router from '@/router'
import common from '@/util/common'

export default {
  name: 'Logout',
  data () {
    return {
      sales: {},
      emails: {},
      stores: {},
      products: {},
      usernames: {},
      usernamesById: {}
    }
  },
  props: [],
  components: {

  },
  methods: {
    date (d) {
      return common.timeAgo(d)
    },
    goTo (route) {
      router.push(route)
    },
    getAllSales () {
      if (this.$store.getters['AUTH.uid']) {
        this.$store.dispatch('AUTH.getAllSales', {}).then((res) => {
          this.sales = res
        })
      } else {
        setTimeout(() => {
          this.getAllSales()
        }, 100)
      }
    },
    getAllEmailsCollected () {
      if (this.$store.getters['AUTH.uid']) {
        this.$store.dispatch('AUTH.getAllEmailsCollected', {}).then((res) => {
          console.log('collectedEmails', res)
          this.emails = res
        })
      } else {
        setTimeout(() => {
          this.getAllEmailsCollected()
        }, 100)
      }
    },
    getAllStores () {
      if (this.$store.getters['AUTH.uid']) {
        this.$store.dispatch('AUTH.getAllStores', {}).then((res) => {
          console.log('stores', res)
          this.stores = res
        })
      } else {
        setTimeout(() => {
          this.getAllStores()
        }, 100)
      }
    },
    getAllProducts () {
      if (this.$store.getters['AUTH.uid']) {
        this.$store.dispatch('AUTH.getAllProducts', {}).then((res) => {
          console.log('products', res)
          this.products = res
        })
      } else {
        setTimeout(() => {
          this.getAllProducts()
        }, 100)
      }
    },
    getAllUsernames () {
      if (this.$store.getters['AUTH.uid']) {
        this.$store.dispatch('AUTH.getAllUsernames', {}).then((res) => {
          let usernamesById = {

          }
          for (let username in res) {
            usernamesById[res[username]] = username
          }
          this.usernames = res
          this.usernamesById = usernamesById
        })
      } else {
        setTimeout(() => {
          this.getAllUsernames()
        }, 100)
      }
    },
  },
  computed: {

  },
  created () {
    this.getAllSales()
    this.getAllEmailsCollected()
    this.getAllStores()
    this.getAllProducts()
    this.getAllUsernames()
  }
}
</script>

<style scoped lang="scss">
  .admin-wrapper {
  }
  .admin-section {
    max-width: 800px;
    padding: 30px;
  }
  .one-sale {
    margin-left: 20px;
  }
  .go-to-button {
    margin-left: 20px;
    cursor: pointer;
    font-weight: 400;
  }
  .ml20 {
    margin-left: 20px;
  }
  .product-image {
    margin-right: 20px;
    width: 100px;
    height: 100px;
    border-radius: 10px;
  }
</style>



