<template>
  <div class="app-wrap df fdc jcsb aic">
    <!-- <DashHeader /> -->
    <DashHeader :mode ="mode"
      :sidebarShown="sidebarShown"
      v-if="(mode !== 'pay') && 
              (mode !== 'login') && 
              (mode !== 'logout') && 
              (mode !== 'start') && 
              (mode !== 'admin') && 
              (mode !== 'terms') && 
              (mode !== 'plan') && 
              (mode !== 'plans') && 
              (mode !== 'privacy') && 
              (mode !== 'link-in-bio') && 
              (mode !== 'link-in-bio-2') && 
              (mode !== 'link-in-bio-3') && 
              (mode !== 'contact')" 
      :setSidebarShown="setSidebarShown"
      class="header" />

    <div class="sidebar-bg"
      v-if="sidebarShown && (mode !== 'live2')"
      @click="setSidebarShown(false, 'a')"></div>

    <div class="global-popup onboarding-popup" @click="closeOnboardingPopup()" v-if="$store.getters['AUTH.onboaradingPopup'].open">
      <div class="global-popup-box" @click="stopPropagation($event)">          
        <div class="whole-page-box-global-2 whole-page-box-global-2-bigger hu popup-box df fdc jcc aic posrel">
          <i @click="closeOnboardingPopup()" class="fa-solid fa-x x-close curp"></i>

          <div class="df fdc mw_">
            <h2 class="text-dark title-right mb40">Connect a bank account to start receiving payments</h2>

            <h4 class="text-dark mb20">Select your country</h4>
            <select class="button-2 w100p" v-model="country" placeholder="Select a country">
              <option :value="''"></option>
              <!-- <option :value="''">Select your country</option> -->
              <option :value="country.code" v-for="country in payoutCountries">{{country.name}}</option>
            </select>
            <p v-if="onboardError" class="onboard-error mt5">{{onboardError}}</p>
            <h4 v-if="!loadingOnboardingAction" class="button-1 new-product-button m0z w100p mt20" @click="startOnboarding()">
                Go
            </h4>
            <h4 v-else class="button-1 new-product-button m0z w100p mt20">
                <img class="spinner-1" src="../../static/images/pushable/loader-black.png">
            </h4>
          </div>
        </div>
      </div>

    </div>
    <div class="global-popup" @click="closeGlobalPopup()" v-if="$store.getters['AUTH.globalPopup'].heading">
      <div class="global-popup-box">          
        <div class="whole-page-box-global-2 whole-page-box-global-2-bigger hu popup-box df fdc jcc aic posrel" @click="stopPropagation($event)">

          <i @click="closeGlobalPopup()" class="fa-solid fa-x x-close curp"></i>

          <h3 class="mb20 mt10 text-dark">{{$store.getters['AUTH.globalPopup'].heading}}</h3>
          <p class="mb30 text-dark">{{$store.getters['AUTH.globalPopup'].text}}</p>

          <h4 v-if="!loadingGlobalAction" class="button-1 mb20 w100p" @click="doGlobalAction($store.getters['AUTH.globalPopup'])">{{$store.getters['AUTH.globalPopup'].callToAction}}</h4>
          <h4 v-else="!loadingGlobalAction" class="button-1 mb20 w100p posrel">
            <img class="spinner-1 o-spinner" src="../../static/images/pushable/loader-black-3.png">
          </h4>

          <h4 v-if="!loadingGlobalAction" class="button-2 mb20 w100p" @click="doGlobalAction2($store.getters['AUTH.globalPopup'])">{{$store.getters['AUTH.globalPopup'].callToAction2}}</h4>
          <h4 v-else class="button-2 mb20 w100p">{{$store.getters['AUTH.globalPopup'].callToAction2}}</h4>

          <h4 v-else class="button-1 mb20">
            <img src="../../static/images/special-agent-v1/loader-white.png" class="loading-button"/>
          </h4>
        </div>
        <!-- <FullSubmission :openSubmissionId="openSubmissionId" /> -->
      </div>
    </div>

    <LoginHeader
        v-if="(mode == 'login') || 
              (mode == 'privacy') || 
              (mode == 'terms')"
        :mode ="mode"
        class="header" />
    <!-- <Header :mode="mode" /> -->
    <FulfillDigitalOrderPopup v-if="$store.getters['AUTH.digitalOrderPopup'] !== null" />

    <Sidebar :sidebarShown="true" 
      :setSidebarShown="setSidebarShown" 
      v-if="(showSidebar.indexOf(mode) > -1) &&
            ($store.getters['AUTH.myInfo'] && $store.getters['AUTH.myInfo'].displayName)
           "
      class="sidebar"/>

    <PayPage v-if="mode == 'pay'" />
    <LinkInBioDisplay v-if="mode == 'link-in-bio'" />
    <LinkInBioDisplay2 v-if="mode == 'link-in-bio-2'" />
    <LinkInBioDisplay3 v-if="mode == 'link-in-bio-3'" />
    
    <div class="f1 df fdc jcsb aic w100p posrel"
          v-bind:class="{
            'app-side': (mode !== 'login') &&
                        (mode !== 'start') &&
                        (mode !== 'privacy') &&
                        (mode !== 'terms') &&
                        (mode !== 'admin') &&
                        (mode !== 'plan') &&
                        (mode !== 'plans') &&
                        ($store.getters['AUTH.myInfo'] && $store.getters['AUTH.myInfo'].displayName)
          }"
          v-if="(mode !=='pay') &&
                (mode !== 'link-in-bio') &&
                (mode !== 'link-in-bio-2') &&
                (mode !== 'link-in-bio-3')
                ">

      <PickAUsername 
                v-if="!($store.getters['AUTH.myInfo'] && $store.getters['AUTH.myInfo'].displayName) &&
                (mode !=='pay') &&
                (mode !=='login') &&
                (mode !=='logout') &&
                (mode !=='start') &&
                (mode !=='privacy') &&
                (mode !=='admin') &&
                (mode !=='terms') &&
                (mode !=='plan') &&
                (mode !=='plans') &&
                (mode !=='link-in-bio') &&
                (mode !=='link-in-bio-2') &&
                (mode !=='link-in-bio-3')
                "
                />

      <Landing v-if="mode == 'landing'" />
      <Dashboard v-if="mode == 'dashboard'" />
      <LinkInBio v-if="mode == 'linkinbio'" />
      <Shipping v-if="mode == 'shipping'" />
      <Audience v-if="mode == 'audience'" />
      <Inventory v-if="mode == 'inventory'" />
      <Settings v-if="mode == 'settings'" />
      <More v-if="mode == 'more'" />
      <Products v-if="mode == 'products'" />
      <Income v-if="mode == 'income'" />
      <Fulfillment v-if="mode == 'fulfillment'" />
      <Appointments v-if="mode == 'appointments'" />
      <AddProduct v-if="mode == 'add-product'" />

      <Product v-if="mode == 'product'" :feel="feelProduct" />
      <Account v-if="mode == 'account'" />
      <Admin v-if="mode == 'admin'" />
      <Login v-if="mode == 'login'" />
      <Logout v-if="mode == 'logout'" />
      <Plan v-if="mode == 'plan'" />
      <Plans v-if="mode == 'plans'" />
      <Start v-if="mode == 'start'" />
      <Faq v-if="mode == 'faq'" />
      <Privacy v-if="mode == 'privacy'" />
      <Terms v-if="mode == 'terms'" />
      
    </div>

    <Footer v-if="(hideFooter.indexOf(mode) < 0)" :mode="mode" />
  </div>
</template>

<script>

import Landing from './Landing'
import Dashboard from './Dashboard'
import LinkInBio from './LinkInBioPlus'
import LinkInBioDisplay from './LinkInBioDisplay'
import LinkInBioDisplay2 from './LinkInBioDisplay2'
import LinkInBioDisplay3 from './LinkInBioDisplay3'
import Shipping from './Shipping'
import Audience from './Audience'
import Inventory from './Inventory'
import Settings from './Settings'
import More from './More'
import Income from './Income'
import Products from './Products'
import Appointments from './Appointments'
import Fulfillment from './Fulfillment'
import Product from './Product'
import AddProduct from './AddProduct'
import Account from './Account'
import Login from './Login'
import Logout from './Logout'
import Plan from './Plan'
import Plans from './Plans'
import Start from './Start'
import Faq from './Faq'
import Header from './Header'
import DashHeader from './DashHeader'
import LoginHeader from './LoginHeader'
import Footer from './Footer'
import Sidebar from './Sidebar'
import Privacy from './Privacy'
import Terms from './Terms'
import PickAUsername from './PickAUsername'
import PayPage from '@/components/paypage/PayPage'
import FulfillDigitalOrderPopup from './FulfillDigitalOrderPopup'
import Admin from './Admin'

import common from '@/util/common'

export default {
  name: 'AppWrap',
  data () {
    return {
      onboardError: '',
      loadingOnboardingAction: false,
      payoutCountries: [],
      country: '',
      loadingGlobalAction: false,
      sidebarShown: false,
      feelProduct: '',
      hideFooter: [
        'account',
        'login',
        'start',
        'plan',
        'admin',
        'plans',
        'terms',
        'privacy',
        'pay',
        'link-in-bio',
        'link-in-bio-2',
        'link-in-bio-3'
      ],
      showSidebar: [
        'dashboard',
        'linkinbio',
        'shipping',
        'audience',
        'inventory',
        'settings',
        'more',
        'income',
        'products',
        'appointments',
        'add-product',
        'fulfillment'
      ],
    }
  },
  props: ['mode'],
  components: {
    Landing,
    Dashboard,
    LinkInBio,
    LinkInBioDisplay,
    LinkInBioDisplay2,
    LinkInBioDisplay3,
    Shipping,
    Audience,
    Inventory,
    Settings,
    More,
    Income,
    Products,
    Appointments,
    Fulfillment,
    Product,
    AddProduct,
    Account,
    Login,
    Logout,
    Plan,
    Plans,
    Start,
    Faq,
    Header,
    DashHeader,
    LoginHeader,
    Footer,
    Sidebar,
    PayPage,
    Privacy,
    Terms,
    PickAUsername,
    FulfillDigitalOrderPopup,
    Admin
  },
  methods: {
    startOnboarding () {
      this.onboardError  = ''
      if (!this.country) {
        this.onboardError = 'Please select a country'
        return false
      }
      this.loadingOnboardingAction = true
      this.$store.dispatch('AUTH.startOnboarding', {country: this.country}).then(async (res) => {
        console.log('startOnboarding', res)
        if (res.url) {
          window.location = res.url
        }
      })
    },
    finishOnboarding () {
      // alert('Finish Onboarding')
      // this.$store.dispatch('AUTH.finishOnboarding', {}).then(async (res) => {
      //   console.log('finishOnboarding', res)
      //   if (res.url) {
      //     window.location = res.url
      //     // this.goTo(res.url)
      //   }
      // })
    },
    doGlobalAction (globalPopup) {
      let useSpinner = globalPopup.loadingSpinner
      let action = globalPopup.action
      if (useSpinner) {
        this.loadingGlobalAction = true
        action()
      }
    },
    doGlobalAction2 (globalPopup) {
      let action = globalPopup.action2
      console.log('action', action)
      action()
      this.closeGlobalPopup()
    },
    stopPropagation (event) {
      event.stopPropagation()
    },
    closeOnboardingPopup () {
      this.loadingOnboardingAction = false
      this.$store.dispatch('AUTH.setOnboardingPopup', {})
    },
    closeGlobalPopup () {
      this.loadingGlobalAction = false
      this.$store.dispatch('AUTH.setGlobalPopup', {})
    },
    setSidebarShown (v, m) {
      console.log('setSidebarShown', v, m)
      this.sidebarShown = v
    },
    checkIfSidebarShouldBeCollapsed () {
      let width = window.innerWidth
      let diff = this.lastWidth - width
      if (diff > 0) { // sizing down
        if ((width < 980) && (this.lastWidth >= 980)) {
          this.sidebarShown = false
        }
      } else {
        if ((this.lastWidth < 980) && (width >= 980)) {
          this.sidebarShown = true
        }
      }
      this.lastWidth = width
    },
    loadProduct () {
      let hash = this.$route.hash
      hash = hash.substring(1, hash.length)
      this.feelProduct = hash
    },
    loadPayoutCountries () {
      let isoCountryCodes = common.isoCountries
      let payoutCountries = common.payoutCountries.map((v) => {
        return {name: v, code: isoCountryCodes[v]}
      })
      this.payoutCountries = payoutCountries
    }
  },
  computed: {

  },
  watch: {
    $route (to, from) {
      if (to !== from) {
        this.loadProduct()
      }
    }
  },
  mounted () {
  },
  created () {
    this.loadProduct()
    this.loadPayoutCountries()
    // checkIfSidebarShouldBeCollapsed
  }
}
</script>

<style scoped lang="scss">
  @import "../styles/settings.scss";

  .app-wrap {
    /*min-height: 100vh;*/
    /*max-width: $max-width;*/
    width: 100%;
  }

  .app-side {
    min-height: calc(100vh - 80px);
    width: 100%;
    background-color: #FAFAFA;
  }

  .sidebar-bg {
    position: fixed;
    z-index: 100;
    background-color: #00000033;
    left: 0;
    top: 0;
    width: 100%;
    height: 100vh;
  }
  @media (max-width: 979px) {
    .app-side {
      /*margin-top: 80px;*/
    }
    /*.app-wrap {
      flex-direction: row;
    }
    .sidebar-bg {
      display: none;
    }*/
  }
  @media (min-width: 980px) {
    .app-side {
      max-width: calc(100% - 250px);
      margin-left: 250px;
    }
  }

  .no-top-margin {
    padding-top: 30px;
    padding-bottom: 30px;
    margin-top: 0;
    justify-content: center;
    width: 100% !important;
    max-width: 100% !important;
    margin-left: 0 !important;
  }

  .hu {
    height: unset;
  }

  .x-close {
    position: absolute;
  }
  .m0z {
    margin-left: 0 !important;
  }
  .mw_ {
    max-width: 300px;
  }
  .spinner-1 {
    width: 20px;
    pointer-events: none;
    animation-name: spin;
    animation-duration: 5000ms;
    animation-iteration-count: infinite;
    animation-timing-function: linear; 
  }

  @keyframes spin {
      from {
          transform:rotate(0deg);
      }
      to {
          transform:rotate(360deg);
      }
  }

  .onboard-error {
    font-size: 14px;
    color: #C84545;
    /*margin-bottom: 20px;*/
  }

  /*@media (max-width: 480px) {
    .app-side {
      margin-top: 80px;
    }
  }*/
</style>



