<template>
  <p class="paybox-price df aic" v-if="data">
    <span class="df aic" v-if="productType == 'physical-product'">
      <select :value="quantity" class="quantity-tab" @change="setQuantity($event)">
        <option :value="1">1</option>
        <option :value="2">2</option>
        <option :value="3">3</option>
        <option :value="4">4</option>
        <option :value="5">5</option>
        <option :value="6">6</option>
        <option :value="7">7</option>
        <option :value="8">8</option>
        <option :value="9">9</option>
        <option :value="10">10</option>
      </select>
      <p class="quantity-x">x</p>
    </span>
    <span>
      ${{data}}
    </span>
  </p>
  <p v-else></p>
</template>

<script>
export default {
  name: 'PayBoxPrice',
  data () {
    return {

    }
  },
  props: ['data', 'quantity', 'setQuantity', 'productType'],
  components: {

  },
  methods: {

  },
  computed: {

  },
  created () {
  }
}
</script>

<style scoped lang="scss">
.paybox-price {
  font-size: 16px;
}
.quantity-tab {
  font-size: 16px;
  width: 50px;
  min-width: 50px;
  margin-right: 10px;
}
.quantity-x {
  margin-right: 10px;
}
</style>



