<script>

import router from '@/router'

export default {
  name: 'Login',
  data () {
    return {
      loginMode: 'login',
      loading: false,
      email: '',
      password: '',
      password2: ''
    }
  },
  metaInfo: {
    // Children can override the title.
    title: 'Pushable - Sell Online Without a Website',
    // Result: My Page Title ← My Site
    // If a child changes the title to "My Other Page Title",
    // it will become: My Other Page Title ← My Site
    // titleTemplate: '%s ← My Site',
    // Define meta tags here.
    meta: [
      {property: 'og:title', content: 'Pushable - Sell Online Without a Website'},
      // {property: 'og:title', content: 'Plans ← My Site'},
      {property: 'og:site_name', content: 'Pushable'},
      // The list of types is available here: http://ogp.me/#types
      {property: 'og:type', content: 'website'},
      // {http-equiv: 'Content-Type', content: 'text/html; charset=utf-8'},
      // {name: 'viewport', content: 'width=device-width, initial-scale=1'},
      {name: 'description', content: 'Sell directly to your customers from social media, blog or anywhere else. Imagine not having to make a website at all. Start selling with the free plan.'}
    ],
    link: [
      {rel: 'canonical', href: 'https://www.pushableapp.com'}
    ]
  },
  props: [],
  components: {
  },
  methods: {
    goTo (route) {
      router.push(route)
    },
    loginWithGoogle () {
      this.$store.dispatch('AUTH.loginWithGoogle')
    },
    login () {
      this.$store.dispatch('AUTH.login', {
        email: this.email,
        password: this.password,
      })
    },
    register () {
      this.$store.dispatch('AUTH.register', {
        email: this.email,
        password: this.password
      })
    },
    setLoginMode (loginMode) {
      this.loginMode = loginMode
    }
  },
  computed: {
  },
  watch: {
  },
  created () {
    console.log('* Landing page created')
  }
}
</script>

